.resume {
    bottom: 1vh;
    right: 1vh;
    padding: 0 1vw;
    width: max-content;
    height: max-content;
    position: fixed;
    z-index: 10;
    border-radius: 5px;
    line-height: 6.5vh;
    text-align: center;
    text-decoration: none;
    color: white;
    transition: all 0.2s ease 0s;
    background: linear-gradient(134deg, rgb(106, 152, 240) 0%, rgb(73, 97, 220) 99%);
    opacity: 0.8;
}

.resume:hover {
    transition-delay: 0.08s;
    color: white;
    opacity: 0.99;
    transform: scale(1.05);
}

.icon {
    margin-right:10px;
}